.Input {
    padding: 2%;
    width: 100%;
    border-radius: 1px;
    border: 1px solid;
}

.StandardInputBorderColor {
    border-color: #D8D8DA;
}

.Input::-webkit-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.Input:-ms-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.Input::-ms-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.Input::placeholder {
    color: #DADADA;
    font-size: 16px;
}



