.ShortInput {
    width: 18%;
    height: 40px;
    padding: 2%;
    border-radius: 1px;
}

.ShortInputBorderColor {
    border: 1px solid #D8D8DA;
}

.ShortInput::-webkit-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.ShortInput:-ms-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.ShortInput::-ms-input-placeholder {
    color: #DADADA;
    font-size: 16px;
}

.ShortInput::placeholder {
    color: #DADADA;
    font-size: 16px;
}

@media only screen and (max-width: 480px) {
    .ShortInput {
        -ms-flex-direction: row;
            flex-direction: row;
        width: 50%;
    }
}



